  padding: 0px 15px 10px;
  display: flex; }
  .filter-buttons .icon {
    width: 22px;
    height: 22px;
    opacity: 0;
    color: var(--color-grey);
    cursor: pointer; }
    .filter-buttons .icon:hover {
      opacity: 1;
      color: white; }
    .filter-buttons .icon.active {
      opacity: 1; }
    .filter-buttons .icon.date {
      flex: 0 1 100px; }
    .filter-buttons .icon.text {
      flex: 1; }
    .filter-buttons .icon.amount {
      flex: 0 1 75px; }

.flow-list {
  flex: 0 1 auto;
  padding: 0px 15px; }
  .flow-list ul li {
    text-decoration: none;
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    margin-top: 35px; }
    .flow-list ul li.flow-item {
      cursor: pointer; }
    .flow-list ul li span.date {
      flex: 0 0 92px;
      color: white;
      font-weight: bold; }
    .flow-list ul li span.text {
      padding: 0px 8px;
      word-break: break-word;
      color: white;
      min-width: 0;
      flex: 1 1 auto; }
      .flow-list ul li span.text .tooltip-wrapper {
        position: relative;
        font-size: .6em; }
        .flow-list ul li span.text .tooltip-wrapper div.usageText {
          color: #9E9E9E;
          width: 100%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          display: inline-block; }
          .flow-list ul li span.text .tooltip-wrapper div.usageText:hover + .tooltip {
            visibility: visible;
            opacity: 1; }
        .flow-list ul li span.text .tooltip-wrapper .tooltip {
          visibility: hidden;
          opacity: 0;
          width: 100%;
          background-color: var(--color-grey-dark);
          color: #fff;
          text-align: center;
          padding: 5px 10px;
          border-radius: 6px;
          white-space: normal;
          position: absolute;
          z-index: 1;
          transition: opacity .2s linear;
          transition-delay: .2s;
          font-size: 1.2em; }
          .flow-list ul li span.text .tooltip-wrapper .tooltip.last {
            bottom: 100%; }
    .flow-list ul li div.amount-wrapper {
      flex: 1 0 auto;
      text-align: right; }
      .flow-list ul li div.amount-wrapper div.merged-item-count {
        color: rgba(255, 255, 255, 0.7);
        font-size: .9em;
        width: 100%;
        text-align: center;
        cursor: pointer; }
        .flow-list ul li div.amount-wrapper div.merged-item-count:hover {
          background-color: rgba(255, 255, 255, 0.3); }
        .flow-list ul li div.amount-wrapper div.merged-item-count .close-botton {
          width: 19px;
          height: 19px;
          margin: 0px auto; }
    .flow-list ul li.merged-items {
      margin-top: 10px;
      background-color: var(--color-grey-dark);
      opacity: .7;
      overflow: hidden;
      padding: 15px 5px; }
    .flow-list ul li.submenu {
      background-color: var(--color-grey-dark);
      margin-top: 10px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 10px;
      justify-items: streched; }
      .flow-list ul li.submenu .item {
        color: white;
        cursor: pointer;
        padding: 5px 0px;
        text-align: center; }
        .flow-list ul li.submenu .item:hover {
          background-color: rgba(255, 255, 255, 0.03); }
  .flow-list ul li:first-of-type {
    margin-top: 0px; }
  .flow-list.sublist ul li span.text .tooltip-wrapper div.usageText {
    color: white;
    font-size: 1em;
    width: 100%;
    overflow: hidden;
    white-space: normal;
    text-overflow: unset; }
  .flow-list.sublist span.date {
    font-weight: normal; }

.total-amount {
  margin-top: auto;
  padding-top: 10px;
  text-align: center;
  font-weight: bold; }

/* width */
