/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; }

.flow-item {
  padding-left: 8px;
  border-radius: 5px; }
  .flow-item.with-periodical {
    border-left: 6px solid var(--color-main); }
    .flow-item.with-periodical.amount-unequal {
      border-left-style: double; }

