          margin: 0px auto; }
    .flow-list ul li.merged-items {
      margin-top: 10px;
      background-color: var(--color-grey-dark);
      opacity: .7;
      overflow: hidden;
      padding: 15px 5px; }
    .flow-list ul li.submenu {
      background-color: var(--color-grey-dark);
      margin-top: 10px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 10px;
      justify-items: streched; }
      .flow-list ul li.submenu .item {
        color: white;
        cursor: pointer;
        padding: 5px 0px;
        text-align: center; }
        .flow-list ul li.submenu .item:hover {
          background-color: rgba(255, 255, 255, 0.03); }
  .flow-list ul li:first-of-type {
    margin-top: 0px; }
  .flow-list.sublist ul li span.text .tooltip-wrapper div.usageText {
    color: white;
    font-size: 1em;
    width: 100%;
    overflow: hidden;
    white-space: normal;
    text-overflow: unset; }
  .flow-list.sublist span.date {
    font-weight: normal; }

.total-amount {
  margin-top: auto;
  padding-top: 10px;
  text-align: center;
  font-weight: bold; }

